import {domReady} from '@roots/sage/client';

// Import Bootstrap
import 'bootstrap';

/**
 * app.main
 */
const main = async (err) => {
  if (err) {
    // handle hmr errors
    console.error(err);
  }

  if (document.querySelector('.swiper')) {
    /*
    let allLinks = document.querySelectorAll('.swiper a');
    allLinks.forEach(link => {
      link.removeEventListener("mousedown").addEventListent("mousedown", function (e) {
        e.preventDefault(); e.stopImmediatePropagation();
      });
    });*/
    jQuery(".swiper").find("a").off("mousedown").on("mousedown", function (e) { e.preventDefault(); e.stopImmediatePropagation(); });
  }
  
  // application code
  if (document.querySelector('.hero-slider')) {
    const swiper = new Swiper('.hero-slider', {
      speed: 400,
      effect: 'fade',
      fadeEffect: {
        crossFade: true
      },
      slidesPerView: 1,
      loop: true,
      pagination: {
        el: ".swiper-dots",
        clickable: true,
      },
    });
  }

  if (document.querySelector('.products-slider')) {
    let allSliders = document.querySelectorAll('.products-slider');
    allSliders.forEach(sliderWrapper => {
      let slider = sliderWrapper.querySelector(".slider");
      let navNext = sliderWrapper.querySelector(".slider__navigation-next");
      let navPrev = sliderWrapper.querySelector(".slider__navigation-prev");
      const swiper = new Swiper(slider, {
        speed: 400,
        slidesPerView: 'auto',
        loop: false,
        shortSwipes: false,
        navigation: {
          nextEl: navNext,
          prevEl: navPrev
        },
      });
    });
  }
};

/**
 * Initialize
 *
 * @see https://webpack.js.org/api/hot-module-replacement
 */
domReady(main);
import.meta.webpackHot?.accept(main);
